export default {
  props: {
    isChecked: {
      type: Boolean,
      default () {
        return false
      }
    },
    index: {
      type: Number,
      default: 0
    },
    selectType: {
      type: String,
      default: 'single'
    },
    item: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  data () {
    return {
      checked: false
    }
  },
  methods: {
    setChecked (checkedValue) {
      this.checked = !this.checked
    },
    setCheckedDefault (checkedValue) {
      this.checked = false
    }
  },
  watch: {
    checked (newChecked) {
      this.$emit('selectFlowItem', this.item, this.checked, this.index)
    }
  }
}



